@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200&display=swap);
*{
  margin:0;
  padding:0;
  border:0;
  box-sizing:border-box;
}

body{
  font-family: 'Poppins', sans-serif;
  
}

.container{
  max-width:800px;

 background-repeat: no-repeat;
 background-size: 800px 950px;
 margin:0 auto;

}

h1,h3{
  color:#000;
}
#informacion{
  display:flex;
  flex-direction:row;
}

p{
  text-align:justify;
}


.contenedor{
  max-width: 600px;
  background-color: rgba(194, 105, 253, 0.692);
  margin-top: 15%;
  margin-left: 35%;
 
  border-radius: 25px;
  min-height: 300px;
  padding: 2rem;
  
}
.titulo{
  margin-left:160px;
  margin-bottom: 5rem;

}
.volver{
  padding: 10px 30px;
  background-color: rgb(237, 234, 245);
  margin: 60px;
  color: #000;
  border-radius: 10px;
  
}
.error{
 
  max-height: 900px;
  margin-left: 25%;
  max-width: 900px;
}

header{
   padding-bottom: 3rem;
   background-image: url(data:image/png;base64,);
    background-repeat:no-repeat;
    display:flex;
    
   
}

.foto{
   
    flex-basis:30%;
    text-align:center;
    
}

.foto img{
    max-height: 130px;
    border-radius:5%;
     margin-top: 13px;
}

.texto{
    flex-basis:70%;
    margin-top: 2rem;
   
    
}

.texto h3{
    max-width:100% ;
    color: rgb(202, 199, 199);
    
}
.texto h1{

    color: rgb(245, 238, 238);
   
}


.contacto{
   
    flex-basis:90%;
    padding-left:5rem;
}
.dat1{
    float: left;
    margin-left: 5rem;
    
}
.dat{
    float: left;
    overflow: hidden;
    margin-left: 5rem;
}
.info{
    margin-bottom:10px;
}

.info i{
    
    padding:10px;
    border-radius:50%;
    margin-right:10px;
    font-size:20px;
}

.ref i{
    font-size:2em;
    color: #274257;
    padding:10px;
}

.refnombre{
    font-weight:500;
}

.refcargo{
    padding-left:60px;
}

.enlaces img{
    max-width:150px;
    margin-left: 40%;
    
}


h2{
    text-align:left;
    color: #7a7c7e;
    margin-bottom: 10px;
    
}

hr{
    border-bottom:2px solid #4a4f53;
    width:100%;
    margin:5px auto;
}

.lugar{
    float: left;
    overflow: hidden;
    color: #4a4f53;
    
    margin-right: 2rem;
   max-width:35% ;
    
    text-align:right;
}
.espe{
    float: left;
    max-width:60% ;
    text-decoration:underline;
    
}
footer{
    background-image: url(data:image/png;base64,);
    min-height:100px;
}
